* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  display: flex;
  justify-content: center;
}

.App {
  width: 600px;
}

.post {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-color: black;
  border-style: solid;
  border-width: 1px;
  margin-top: 15px;
  transition: all 1500ms ease-in;
}

.post__btns {
  display: flex;
}

.post-enter {
  opacity: 0.5;
  transform: translateX(-350px);
  background-color: rgb(255, 0, 68);
  transition: all 500ms ease-in;
}

.post-enter-active {
  opacity: 1;
  transform: translateX(0px);
  transition: all 500ms ease-in;
  background-color: blue;
}

.post-exit {
  opacity: 1;
  transition: all 500ms ease-in;
}

.post-exit-active {
  opacity: 0.5;
  transform: translateX(350px);
  transition: all 500ms ease-in;
  background-color: green;
}

.page {
  padding: 5px 15px;
  color: teal;
  font-size: 14px;
  background: transparent;
  border: 1px solid teal;
  cursor: pointer;
  margin-top: 5px;
  transition: all 500ms;
  margin-bottom: 10px;
}

.page__current {
  background-color: orange;
}

.page:hover {
  color: white;
  background-color: black;
}

.navbar {
  height: 50px;
  width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
}

.navbar__links {
  color: auto;
}
